import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  IFieldOptions,
  BlurEvent,
  MetaDataType,
  MetaDataLookupMapping,
  MetaDataLookup,
  FixedDataSource,
} from 'shared-code-lib';

@Component({
  selector: 'app-lookup-mappings',
  templateUrl: './lookup-mappings.component.html',
  styleUrls: ['./lookup-mappings.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class LookupMappingsComponent implements OnInit {
  @Input() lookup: MetaDataLookup;
  @Input() lookupFieldOptions: IFieldOptions[];
  @Input() metaDataMappingOptions: IFieldOptions[];

  fieldLookupField: FieldDefinition;
  lookupFieldDataSource: FixedDataSource;
  metaDataMappingDataSource: FixedDataSource;

  mappingFields = {};
  notMappedOption: IFieldOptions = { name: this.translate.instant('admin-metadata-lookup.mapping-none'), value: '' };

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.metaDataMappingDataSource = new FixedDataSource(this.metaDataMappingOptions);

    this.lookupFieldDataSource = new FixedDataSource(this.lookupFieldOptions);
    this.lookupFieldOptions.unshift(this.notMappedOption);

    this.createFields();
  }

  createFields = (): void => {
    this.fieldLookupField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-metadata-lookup.lookup-field'),
      placeholder: this.translate.instant('admin-metadata-lookup.lookup-field'),
      datasource: this.lookupFieldDataSource,
      required: true,
    });
  };

  getFieldMappingField = (field: IFieldOptions): FieldDefinition => {
    if (!this.mappingFields[field.value]) {
      this.mappingFields[field.value] = new FieldDefinition({
        type: MetaDataType.List,
        label: field.name,
        datasource: this.metaDataMappingDataSource,
      });
    }

    return this.mappingFields[field.value];
  };

  getFieldMappingValue = (fieldId: string): string => {
    const mapping = this.lookup.mappings.find((m) => m.fieldId === fieldId);
    return mapping ? mapping.metaDataId : '';
  };

  setFieldMappingValue = (e: BlurEvent, fieldId: string): void => {
    let mapping = this.lookup.mappings.find((m) => m.fieldId === fieldId);

    if (e.value === '') {
      if (mapping) this.lookup.mappings.splice(this.lookup.mappings.indexOf(mapping), 1);
      return;
    }

    if (!mapping) {
      mapping = new MetaDataLookupMapping({
        metaDataId: '',
        overwrite: true,
        fieldId: fieldId,
      });
      this.lookup.mappings.push(mapping);
    }

    mapping.metaDataId = e.value;
  };

  getFieldMappingOverwrite = (fieldId: string): boolean => {
    let mapping = this.lookup.mappings.find((m) => m.fieldId === fieldId);
    return mapping ? mapping.overwrite : true;
  };

  setFieldMappingOverwrite = (fieldId: string, overwrite: boolean): void => {
    let mapping = this.lookup.mappings.find((m) => m.fieldId === fieldId);
    if (!mapping) return;

    mapping.overwrite = overwrite;
  };

  lookupFieldChanged = ($event) => {
    let mapping;
    if (this.lookup.type === 'Upland.Hydra.Database.DataSourcesLookup') {
      mapping = this.lookup.mappings.find((m) => m.fieldId === this.lookup.lookupFieldId);
    } else {
      mapping = this.lookup.mappings.find((m) => m.fieldId == $event);
    }

    if (mapping) {
      this.lookup.mappings.splice(this.lookup.mappings.indexOf(mapping), 1);
    }
  };
}
