import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription, combineLatest } from 'rxjs';

import { IWorkflow } from '../../blocks/models/workflow';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

import {
  CSVLookup,
  DelayService,
  Feature,
  FieldDefinition,
  IFieldDataSource,
  IFieldOptions,
  MetaDataCascadingOptionsDataSource,
  MetaDataCascadingOptionsValidator,
  MetaDataDataSource,
  MetaDataDataSourceColumnValidator,
  MetaDataDataSourceValidator,
  MetaDataDateValidator,
  MetaDataDefinition,
  MetaDataLengthValidator,
  MetaDataListDataSourcee,
  MetaDataNOOPValidator,
  MetaDataNoopLookup,
  MetaDataNotBlankValidator,
  MetaDataRegExValidator,
  MetaDataType,
  MetaDataValidation,
  MetadataIntellisenseDatasource,
  Tab,
} from 'shared-code-lib';
import { UserStateService } from '../../blocks/services/user-state.service';

@Component({
  selector: 'app-admin-metadata',
  templateUrl: './admin-metadata.component.html',
  styleUrls: ['./admin-metadata.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminMetaDataComponent implements OnInit, OnDestroy {
  routeParams: Params;
  routeData: Data;
  listDataSource;

  activeTab = 'general';
  tabs = new Array<Tab>();

  fieldName: FieldDefinition;
  fieldLabel: FieldDefinition;
  fieldType: FieldDefinition;
  fieldValidation: FieldDefinition;
  fieldValidationMessage: FieldDefinition;
  fieldDefaultValue: FieldDefinition;
  fieldGroupingFormat: FieldDefinition;
  fieldExpression: FieldDefinition;

  validatorRegEx: MetaDataRegExValidator;
  validatorLength: MetaDataLengthValidator;
  validatorDate: MetaDataDateValidator;
  validatorDataSourceColumn: MetaDataDataSourceColumnValidator;

  validatorType = 'MetaDataNOOPValidator';
  validationMessage: string;

  validationTypes = new TypeValidationsDataSource(this.translateService);

  get mddType() {
    return `${this.mdd.type}`;
  }
  set mddType(val: string) {
    this.mdd.type = Number(val);
  }
  get mddStickyField(): boolean {
    return Boolean(this.mdd.stickyField);
  }
  set mddStickyField(val: boolean) {
    this.mdd.stickyField = `${val}`;
  }

  getMetaDataDataListDataSourcee = () => this.mdd.dataSource as MetaDataListDataSourcee;
  getMetaDataCascadingOptionsDataSource = () => this.mdd.dataSource as MetaDataCascadingOptionsDataSource;

  rightControls: Array<Control> = [];

  dataSource: MetaDataDataSource = new MetaDataListDataSourcee();
  validation: MetaDataValidation = new MetaDataNOOPValidator();

  mdd = new MetaDataDefinition({
    id: '',
    tenantId: '',
    name: '',
    label: '',
    defaultValue: '',
    allowNoSelection: false,
    hasValidation: false,
    validation: new MetaDataNOOPValidator(),
    dataSource: new MetaDataListDataSourcee(),
    type: MetaDataType.Text,
    lineItemConfigurations: [],
    workflowsAssigned: false,
    assignedWorkflowIds: [],
    formatters: [],
    stickyField: false,
    dateCreatedUTC: new Date(),
    dateModifiedUTC: new Date(),
    groupingFormat: 'MM/dd/yyyy',
    expression: '',
  });

  newMetaDataItem = { name: '', value: '' };

  workflows: Array<IWorkflow>;
  current: Array<IWorkflow> = [];

  saveButton: Control;
  cancelButton: Control;
  currentType: MetaDataType = null;
  dataSourcesBackup = {};

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private delay: DelayService,
    private spinner: SpinnerStateService,
    private userStateService: UserStateService
  ) {}

  ngOnInit() {
    this.createValidators();
    this.createButtons();
    this.createFields();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.loadMetadata();
      })
    );
  }

  getTabs = (): Array<Tab> => {
    const tabs = new Array<Tab>();

    if (this.mdd.type !== MetaDataType.LineItems) {
      tabs.push(new Tab('general', this.translateService.instant('admin-metadata.general')));
    }

    tabs.push(new Tab('assign', this.translateService.instant('admin-metadata.assign')));

    if (this.mdd.type === MetaDataType.Text && this.userStateService.hasFeature(Feature.DateFieldFormatting)) {
      tabs.push(new Tab('formatting', this.translateService.instant('admin-metadata.formatting')));
    }

    if (this.shouldAddItemsTab()) {
      tabs.push(new Tab('items', this.translateService.instant('admin-metadata.items')));
    }

    if (this.mdd.type === MetaDataType.LineItems) {
      tabs.push(new Tab('lineitems', this.translateService.instant('admin-metadata.lineitems')));
    }

    if (this.mdd.type === MetaDataType.Lookup) {
      tabs.push(new Tab('lookup', this.translateService.instant('admin-metadata.lookup')));
    }

    if (this.mdd.type === MetaDataType.CascadingOptions) {
      tabs.push(new Tab('cascading-options', this.translateService.instant('admin-metadata.options')));
    }

    return tabs;
  };

  shouldAddItemsTab = () => {
    return this.mdd.type === MetaDataType.List || this.validatorType === 'MetaDataDataSourceValidator';
  };

  fieldValidatorTypeChanged = (value: string): void => {
    this.tabs = this.getTabs();
  };

  fieldTypeChanged = (value: string): void => {
    const metaDataType = parseInt(value);
    this.mdd.type = metaDataType;

    const validations = this.validationTypes.validations.filter(
      (v) => v.types.length == 0 || v.types.includes(metaDataType)
    );
    if (validations.find((v) => v.value == this.validatorType) == null) {
      this.validatorType = 'MetaDataNOOPValidator';
    }

    this.delay.execute(() => this.validationTypes.setMetaDataType(metaDataType), 50);

    this.tabs = this.getTabs();
    this.activeTab = metaDataType === MetaDataType.LineItems ? 'lineitems' : 'general';

    if (this.currentType != null && this.mdd) this.dataSourcesBackup[this.currentType] = this.mdd.dataSource;
    this.currentType = metaDataType;
    if (this.dataSourcesBackup[metaDataType]) {
      this.mdd.dataSource = this.dataSourcesBackup[metaDataType];
      return;
    }

    if (!(this.mdd.dataSource instanceof MetaDataListDataSourcee) && metaDataType === MetaDataType.List) {
      this.mdd.dataSource = new MetaDataListDataSourcee();
    } else if (
      !(this.mdd.dataSource instanceof MetaDataCascadingOptionsDataSource) &&
      metaDataType === MetaDataType.CascadingOptions
    ) {
      this.mdd.dataSource = new MetaDataCascadingOptionsDataSource();
    }

    if (metaDataType != MetaDataType.Text) {
      this.mdd.formatters = [];
    }
  };

  private createValidators() {
    this.validatorRegEx = new MetaDataRegExValidator();
    this.validatorLength = new MetaDataLengthValidator();
    this.validatorDate = new MetaDataDateValidator();
    this.validatorDataSourceColumn = new MetaDataDataSourceColumnValidator();
  }

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.translateService.get('Metadata.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Metadata.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  onSave = () => {
    const errors = this.validateNewMetaDataItem();
    if (errors.length) {
      this.errorsService.showErrorsModal(errors, 'Metadata-Form.metadata-validation-errors');
      return;
    }

    this.mdd.validation = this.getSelectedValidator();
    this.mdd.validation.validationMessage = this.validationMessage;

    if (this.mdd.type !== MetaDataType.Lookup) this.mdd.lookup = new MetaDataNoopLookup();

    if (this.mdd.id) {
      this.hydraApi.updateMetaDataDefinition(this.mdd, this.metadataSaved, this.metadataSaveFailed);
    } else {
      this.hydraApi.createMetaDataDefinition(this.mdd, this.metadataSaved, this.metadataSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  validateNewMetaDataItem(): string[] {
    if (!this.shouldAddItemsTab() || (!this.newMetaDataItem.name.trim() && !this.newMetaDataItem.value.trim())) {
      return [];
    }

    return ['Metadata-Form.new-item-not-saved'];
  }

  getSelectedValidator = (): MetaDataValidation => {
    if (this.validatorType == 'MetaDataRegExValidator') return this.validatorRegEx;
    else if (this.validatorType == 'MetaDataLengthValidator') return this.validatorLength;
    else if (this.validatorType == 'MetaDataNOOPValidator') return new MetaDataNOOPValidator();
    else if (this.validatorType == 'MetaDataDataSourceValidator') return new MetaDataDataSourceValidator();
    else if (this.validatorType == 'MetaDataDataSourceColumnValidator') return this.validatorDataSourceColumn;
    else if (this.validatorType == 'MetaDataNotBlankValidator') return new MetaDataNotBlankValidator();
    else if (this.validatorType == 'MetaDataCascadingOptionsValidator') return new MetaDataCascadingOptionsValidator();
    else if (this.validatorType == 'MetaDataDateValidator') return this.validatorDate;

    throw new Error(`unknown validator selection ${this.validatorType}`);
  };

  metadataSaved = (result: ApiResult) => {
    if (this.mdd.lookup instanceof CSVLookup) {
      const csvLookup = <CSVLookup>this.mdd.lookup;

      if (csvLookup.csvFile) {
        this.hydraApi.updateCSVLookupData(
          result.object.id,
          csvLookup.csvFile,
          this.metaDataSavedSuccessfully,
          this.metadataSaveFailed
        );

        return;
      }
    }

    this.metaDataSavedSuccessfully();
  };

  metaDataSavedSuccessfully = () => {
    this.toastr.success(this.translateService.instant('Metadata-Form.MetadataDefinition-Saved'));
    this.returnToGrid();
    this.spinner.hide();
  };

  metadataSaveFailed = (err: any) => {
    if (err && err.errors && !err.success) {
      this.errorsService.showErrorsModal(err.errors, 'Metadata-Form.metadata-validation-errors');
      this.spinner.hide();
      return;
    }

    this.toastr.error(this.translateService.instant('Metadata-Form.MetadataDefinition-Save-Failed'));
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.MetaData'
    );
  };

  private loadMetadata = () => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.hydraApi.getMetaDataDefinition(
        this.routeParams.metadataid,
        this.metaDataLoaded,
        this.metaDataDefinitionLoadFailed
      );

      return;
    }

    this.metaDataLoaded(this.mdd);
  };

  private metaDataLoaded = (m: MetaDataDefinition) => {
    if (this.routeData.type === 'duplicate') {
      m.name = `${this.translateService.instant('Admin.CopyOf')} ${m.name}`;
      m.id = '';
    }

    this.validationTypes.setMetaDataType(m.type);
    this.validatorType = m.validation.type;
    this.validationMessage = m.validation.validationMessage;

    if (m.validation.type == 'MetaDataRegExValidator') {
      this.validatorRegEx = <MetaDataRegExValidator>m.validation;
    } else if (m.validation.type == 'MetaDataLengthValidator') {
      this.validatorLength = <MetaDataLengthValidator>m.validation;
    } else if (m.validation.type == 'MetaDataDateValidator') {
      this.validatorDate = <MetaDataDateValidator>m.validation;
    } else if (m.validation.type == 'MetaDataDataSourceColumnValidator') {
      this.validatorDataSourceColumn = <MetaDataDataSourceColumnValidator>m.validation;
    }

    this.mdd = m;
    this.fieldTypeChanged(this.mdd.type.toString());
  };

  private metaDataDefinitionLoadFailed = () => {
    this.toastr.error(this.translateService.instant('Metadata-Form.metadata-load-failed'));
  };

  itemsAreRequired = (): boolean => {
    if (this.validatorType === 'MetaDataDataSourceValidator') return true;
    if (this.mdd.type == MetaDataType.List) return true;

    return false;
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('Metadata-Form.Name'),
      placeholder: this.translateService.instant('Metadata-Form.Name'),
      required: true,
    });
    this.fieldLabel = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('Metadata-Form.DisplayName'),
      placeholder: this.translateService.instant('Metadata-Form.DisplayName'),
      required: true,
    });
    this.fieldType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('Metadata-Form.Type'),
      placeholder: this.translateService.instant('Metadata-Form.Type'),
      required: true,
      datasource: new FieldTypesDataSource(this.translateService, this.userStateService),
    });
    this.fieldValidation = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('Metadata-Form.Validation'),
      placeholder: this.translateService.instant('Metadata-Form.Validation'),
      required: true,
      datasource: this.validationTypes,
    });
    this.fieldValidationMessage = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('Metadata-Form.ValidationMessage'),
      placeholder: this.translateService.instant('Metadata-Form.ValidationMessage-placeholder'),
      required: false,
    });

    this.fieldDefaultValue = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('Metadata-Form.default-value'),
      placeholder: this.translateService.instant('Metadata-Form.default-value-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldGroupingFormat = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('Metadata-Form.grouping-format'),
      placeholder: this.translateService.instant('Metadata-Form.grouping-format-placeholder'),
      required: true,
    });

    this.fieldExpression = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('Metadata-Form.expression-field'),
      placeholder: this.translateService.instant('Metadata-Form.expression-field-placeholder'),
      required: true,
    });
  };

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  onLoadOptions() {}

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}

class ValidationItem {
  constructor(public value: string, public name: string, public types: Array<MetaDataType>) {}
}

class TypeValidationsDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  private metaDataType: MetaDataType;

  constructor(private translate: TranslateService) {}

  validations: Array<ValidationItem> = [
    new ValidationItem('MetaDataLengthValidator', this.translate.instant('Metadata-Form.Length'), [MetaDataType.Text]),
    new ValidationItem('MetaDataDataSourceValidator', this.translate.instant('Metadata-Form.List'), [
      MetaDataType.Text,
      MetaDataType.List,
    ]),
    new ValidationItem('MetaDataDataSourceColumnValidator', 'Data Source Column', [
      MetaDataType.Decimal,
      MetaDataType.Date,
      MetaDataType.Integer,
      MetaDataType.Text,
    ]),
    new ValidationItem('MetaDataNotBlankValidator', this.translate.instant('Metadata-Form.NotBlank'), [
      MetaDataType.Text,
      MetaDataType.Integer,
      MetaDataType.Currency,
      MetaDataType.Decimal,
      MetaDataType.Date,
      MetaDataType.Lookup,
    ]),
    new ValidationItem('MetaDataNOOPValidator', this.translate.instant('Metadata-Form.None'), []),
    new ValidationItem('MetaDataRegExValidator', this.translate.instant('Metadata-Form.Regex'), [
      MetaDataType.Text,
      MetaDataType.Integer,
      MetaDataType.Currency,
      MetaDataType.Decimal,
    ]),
    new ValidationItem('MetaDataCascadingOptionsValidator', this.translate.instant('Metadata-Form.CascadingOptions'), [
      MetaDataType.CascadingOptions,
    ]),
    new ValidationItem('MetaDataDateValidator', this.translate.instant('Metadata-Form.Date'), [MetaDataType.Date]),
  ];

  setMetaDataType = (mdt: MetaDataType): void => {
    this.metaDataType = mdt;
    this.changed.next();
  };

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    const validations = this.validations.filter((v) => v.types.length == 0 || v.types.includes(this.metaDataType));
    successCallBack(validations);
  }
}

export class FieldTypesDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  types: IFieldOptions[] = [];

  constructor(private translate: TranslateService, private userStateService: UserStateService) {
    this.types = [
      { value: '0', name: this.translate.instant('Metadata-Form.Text') },
      { value: '1', name: this.translate.instant('Metadata-Form.LargeText') },
      { value: '3', name: this.translate.instant('Metadata-Form.Integer') },
      { value: '4', name: this.translate.instant('Metadata-Form.Currency') },
      { value: '6', name: this.translate.instant('Metadata-Form.Date') },
      { value: '8', name: this.translate.instant('Metadata-Form.List') },
      { value: '9', name: this.translate.instant('Metadata-Form.LineItems') },
      { value: '10', name: this.translate.instant('Metadata-Form.Decimal') },
      { value: '12', name: this.translate.instant('Metadata-Form.Expression') },
    ];

    if (this.userStateService.hasFeature(Feature.ManageDataSources)) {
      this.types.push({ value: '11', name: this.translate.instant('Metadata-Form.Lookup') });
      this.types.push({ value: '13', name: this.translate.instant('Metadata-Form.CascadingOptions') });
    }

    this.types.sort((x, y) => x.name.localeCompare(y.name));
  }

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    successCallBack(this.types);
  }
}
