import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  LineItemConfiguration,
  MetaDataDefinition,
  MetaDataType,
  ObjectIdGenerator,
} from 'shared-code-lib';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-metadata-lineitems',
  templateUrl: './metadata-lineitems.component.html',
  styleUrls: ['./metadata-lineitems.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MetadataLineitemsComponent implements OnInit {
  @Input() mdd: MetaDataDefinition;

  available: Array<MetaDataDefinition> = [];
  dataFieldsDataSource: FixedDataSource;
  compareToDataSources: FixedDataSource[] = [];
  dataFieldsLoaded: boolean = false;
  newFieldId: string = '';

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit() {
    this.dataFieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
    for (let i = 0; i < this.mdd.lineItemConfigurations.length; i++) {
      this.compareToDataSources.push(new FixedDataSource(new Array<IFieldOptions>()));
    }

    this.setupMetaData();
  }

  setupMetaData(): void {
    this.available = this.userState.user.metaData.filter((mdd) => mdd.type != MetaDataType.LineItems);

    this.updateDataFieldsDataSource();
    this.updateCompareToDataSources();
  }

  updateDataFieldsDataSource = (): void => {
    const metadataFields: Array<IFieldOptions> = [];

    for (let i = 0; i < this.available.length; i++) {
      if (this.mdd.lineItemConfigurations.findIndex((x) => x.metaDataId == this.available[i].id) != -1) continue;

      metadataFields.push({
        name: this.available[i].name,
        value: this.available[i].id,
      });
    }

    this.dataFieldsDataSource.updateOptions(metadataFields);
    this.dataFieldsLoaded = true;
  };

  updateCompareToDataSources = (): void => {
    const metadataFields: Array<IFieldOptions> = [];

    metadataFields.push({
      name: this.translate.instant('Metadata-Form.none'),
      value: ObjectIdGenerator.Empty,
    });

    for (let i = 0; i < this.available.length; i++) {
      if (this.available[i].type != MetaDataType.Integer && this.available[i].type != MetaDataType.Decimal) continue;

      metadataFields.push({
        name: this.translate.instant(this.available[i].name),
        value: this.available[i].id,
      });
    }

    for (let i = 0; i < this.compareToDataSources.length; i++) {
      this.compareToDataSources[i].updateOptions(
        metadataFields.filter(
          (x) => x.name != this.available.find((x) => x.id == this.mdd.lineItemConfigurations[i].metaDataId).name
        )
      );
    }
  };

  createField = (label: string, placeholder: string, datasource: FixedDataSource, hideLabel: boolean = undefined) => {
    return new FieldDefinition({
      type: MetaDataType.List,
      label: label ? this.translate.instant(label) : '',
      placeholder: placeholder ? this.translate.instant(placeholder) : '',
      required: true,
      datasource: datasource,
      hideLabel: hideLabel,
    });
  };

  isNumeric = (metaDataId: string): boolean => {
    const mdd = this.available.find((x) => x.id == metaDataId);
    if (!mdd) return false;

    return mdd.type == MetaDataType.Integer || mdd.type == MetaDataType.Decimal;
  };

  addField = (): void => {
    this.mdd.lineItemConfigurations.push(
      new LineItemConfiguration({
        metaDataId: this.newFieldId,
        compareToMetaDataId: ObjectIdGenerator.Empty,
        showTotal: false,
      })
    );
    this.updateDataFieldsDataSource();
    this.compareToDataSources.push(new FixedDataSource(new Array<IFieldOptions>()));
    this.updateCompareToDataSources();
  };

  deleteItem = (item: LineItemConfiguration): void => {
    const index = this.mdd.lineItemConfigurations.findIndex((x) => x == item);
    if (index != -1) this.mdd.lineItemConfigurations.splice(index, 1);
    this.updateDataFieldsDataSource();
    this.compareToDataSources.splice(index, 1);
    this.updateCompareToDataSources();
  };

  deleteAllItems = (): void => {
    this.mdd.lineItemConfigurations = [];
    this.updateDataFieldsDataSource();
    this.compareToDataSources = [];
    this.updateCompareToDataSources();
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.mdd.lineItemConfigurations, event.previousIndex, event.currentIndex);
  }

  getFieldText = (metaDataId: string): string => {
    const mdd = this.available.find((x) => x.id == metaDataId);
    if (mdd) return mdd.name;
    return '';
  };
}
