import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { FieldDefinition, MetaDataType, MetaDataRegExValidator } from 'shared-code-lib';

@Component({
  selector: 'app-validation-regex',
  templateUrl: './validation-regex.component.html',
  styleUrls: ['./validation-regex.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ValidationRegexComponent implements OnInit {
  @Input() validator: MetaDataRegExValidator;

  fieldPattern: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields = (): void => {
    this.fieldPattern = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('Metadata-Form.pattern'),
      placeholder: this.translate.instant('Metadata-Form.pattern-placeholder'),
      required: true,
    });
  };
}
