import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataDefinition, MetaDataType } from 'shared-code-lib';
import { Workflow } from '../../../blocks/models/workflow';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-metadata-workflows',
  templateUrl: './metadata-workflows.component.html',
  styleUrls: ['./metadata-workflows.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MetadataWorkflowsComponent implements OnInit {
  @Input() mdd: MetaDataDefinition;

  available: Array<Workflow> = [];
  current: Array<Workflow> = [];

  options: IFieldOptions[];
  selectedAssignValue: string;

  fieldAssignTypes: FieldDefinition;

  constructor(private hydraApi: HydraApiService, private translate: TranslateService, private toastr: ToastrService) {}

  ngOnInit() {
    const allWorkflowsOption: IFieldOptions = {
      value: '0',
      name: this.translate.instant('admin-metadata-workflows.all-workflows'),
    };
    const selectedWorkflowsOption: IFieldOptions = {
      value: '1',
      name: this.translate.instant('admin-metadata-workflows.selected-workflows'),
    };

    this.options = [allWorkflowsOption, selectedWorkflowsOption];
    this.selectedAssignValue = this.mdd.workflowsAssigned ? selectedWorkflowsOption.value : allWorkflowsOption.value;

    this.fieldAssignTypes = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-metadata-workflows.assign-metadata-to'),
      placeholder: this.translate.instant('admin-metadata-workflows.assign-metadata-to'),
      datasource: new FixedDataSource(this.options),
    });

    this.loadWorkflows();
  }

  loadWorkflows = (): void => {
    this.hydraApi.getWorkflows(this.workflowsLoaded, this.workflowsFailedLoad);
  };

  workflowsLoaded = (workflows: Workflow[]): void => {
    this.available = workflows;
    this.current = [];

    for (let id of this.mdd.assignedWorkflowIds) {
      const item = workflows.find((w) => w.id === id);
      if (item) this.current.push(item);
    }
  };

  workflowsFailedLoad = (err: any): void => {
    this.toastr.error(this.translate.instant('errors.failed-load-workflows'));
  };

  currentChanged = (workflows: Array<Workflow>): void => {
    this.mdd.assignedWorkflowIds = [];

    for (let workflow of workflows) {
      this.mdd.assignedWorkflowIds.push(workflow.id);
    }
  };

  selectedOptionChanged(optionValue: string): void {
    if (optionValue == '0') {
      this.mdd.workflowsAssigned = false;
      this.mdd.assignedWorkflowIds = [];
    } else {
      this.mdd.workflowsAssigned = true;
    }
  }
}
