import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { FieldDefinition, MetaDataType, MetaDataLengthValidator } from 'shared-code-lib';

@Component({
  selector: 'app-validation-length',
  templateUrl: './validation-length.component.html',
  styleUrls: ['./validation-length.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ValidationLengthComponent implements OnInit {
  @Input() validator: MetaDataLengthValidator;

  minValue: FieldDefinition;
  maxValue: FieldDefinition;

  get minLength() {
    return `${this.validator.minLength}`;
  }
  set minLength(val: string) {
    this.validator.minLength = Number(val);
  }
  get maxLength() {
    return `${this.validator.maxLength}`;
  }
  set maxLength(val: string) {
    this.validator.maxLength = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields = (): void => {
    this.minValue = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('Metadata-Form.Min'),
      placeholder: this.translate.instant('Metadata-Form.Min'),
      required: true,
    });
    this.maxValue = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('Metadata-Form.Max'),
      placeholder: this.translate.instant('Metadata-Form.Max'),
      required: true,
    });
  };
}
