import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataDataSourceColumnValidator,
  MetaDataType,
} from 'shared-code-lib';
import { Log } from '../../../blocks/core/log';
import { IDataSource } from '../../../blocks/models/datasource';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-validation-datasource',
  templateUrl: './validation-datasource-column.component.html',
  styleUrls: ['./validation-datasource-column.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ValidationDataSourceColumnComponent implements OnInit {
  @Input() validator: MetaDataDataSourceColumnValidator;

  allDataSources: IDataSource[];

  datasourcesDataSource: FixedDataSource;
  columnsDataSource: FixedDataSource;
  dataFieldsDataSource: FixedDataSource;

  dataSourceColumnsField: FieldDefinition;
  datasourcesField: FieldDefinition;

  datasourceColumnsLoaded: boolean = false;
  datasourcesLoaded: boolean = false;

  constructor(
    private hydraApi: HydraApiService,
    private translate: TranslateService,
    private spinner: SpinnerStateService
  ) {}

  ngOnInit() {
    this.spinner.show(this.translate.instant('Common.Loading'), document.body);
    this.datasourcesDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.columnsDataSource = new FixedDataSource(
      new Array<IFieldOptions>({
        name: this.translate.instant('Metadata-Form.None'),
        value: this.validator.dataSourceColumnId,
      })
    );
    this.createFields();
    this.hydraApi.getAllDataSources(this.datasourcesLoadSucceeded, this.datasourcesLoadFailed);
  }

  datasourcesLoadSucceeded = (datasources: IDataSource[]): void => {
    this.allDataSources = datasources;
    const columnsFields: Array<IFieldOptions> = [];
    datasources.forEach((datasource) => {
      columnsFields.push({ name: datasource.name, value: datasource.id });
    });

    columnsFields.unshift({ name: this.translate.instant('Metadata-Form.None'), value: '' });
    this.datasourcesDataSource.updateOptions(columnsFields);
    this.onDataSourceChanged(this.validator.dataSourceId);
    this.datasourcesLoaded = true;
    this.spinner.hide();
  };

  datasourcesLoadFailed = (error: any): void => {
    Log.error(this.translate.instant('Metadata-Form.loading-datasources-failed'), error);
    this.spinner.hide();
  };

  columnChanged = (value: string): void => {
    const selectedOption = this.columnsDataSource.options.find((option) => option.value == value);
    this.validator.dataSourceColumnName = selectedOption == null ? '' : selectedOption.name;
  };

  onDataSourceChanged = (value: string): void => {
    if (value == '') {
      this.validator.dataSourceColumnId = '';
      return;
    }

    const columnOptions = new Array<IFieldOptions>();
    const selectedDataSource = this.allDataSources.filter((datasource) => datasource.id === value);
    selectedDataSource[0].columns.forEach((column) => {
      columnOptions.push({ name: column.name, value: column.id });
    });
    columnOptions.unshift({ name: this.translate.instant('Metadata-Form.None'), value: '' });
    this.columnsDataSource.updateOptions(columnOptions);
  };

  createField = (label: string, placeholder: string, datasource: FixedDataSource): FieldDefinition => {
    return new FieldDefinition({
      type: MetaDataType.List,
      label: label ? this.translate.instant(label) : '',
      placeholder: placeholder,
      required: true,
      datasource: datasource,
    });
  };

  createFields = (): void => {
    this.datasourcesField = this.createField('Metadata-Form.datasource', '', this.datasourcesDataSource);
    this.dataSourceColumnsField = this.createField('Metadata-Form.datasourceColumn', '', this.columnsDataSource);
  };
}
