import { Component, OnInit, Input, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { FieldDefinition, MetaDataType, DateValue, FixedDataSource, DateValueType } from 'shared-code-lib';

@Component({
  selector: 'app-date-value',
  templateUrl: './date-value.component.html',
  styleUrls: ['./date-value.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DateValueComponent implements OnInit {
  @Input() date: DateValue;
  @Input() minValue: DateValue;
  @Input() maxValue: DateValue;
  @Output() onValueChange = new EventEmitter();

  dateField: FieldDefinition;
  offsetField: FieldDefinition;
  typeField: FieldDefinition;

  get dateType() {
    return `${this.date.type}`;
  }
  set dateType(val: string) {
    this.date.type = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields = (): void => {
    this.dateField = new FieldDefinition({
      type: MetaDataType.Date,
      label: '',
      placeholder: '',
      required: true,
    });

    this.offsetField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: '',
      placeholder: this.translate.instant('Metadata-Form.days-number'),
      required: true,
      allowNegativeNumbers: false,
    });

    const options = [];
    options.push({ name: this.translate.instant(`Date-Value.days-before`), value: 1 });
    options.push({ name: this.translate.instant(`Date-Value.days-after`), value: 2 });

    this.typeField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: new FixedDataSource(options),
    });
  };

  onChange = (): void => {
    this.onValueChange.emit();
  };
}
