import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { DateValueType, FieldDefinition, FixedDataSource, MetaDataDateValidator, MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-validation-date',
  templateUrl: './validation-date.component.html',
  styleUrls: ['./validation-date.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ValidationDateComponent implements OnInit {
  @Input() validator: MetaDataDateValidator;

  typeField: FieldDefinition;

  _type: DateValueType;
  get type() {
    return `${this._type}`;
  }
  set type(val: string) {
    this._type = Number(val);
    this.validator.changeType(this._type);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.createFields();

    this._type =
      this.validator.beginDate.type == DateValueType.SpecificDate
        ? DateValueType.SpecificDate
        : DateValueType.RelativeDate;
  }

  createFields = (): void => {
    const options = [];

    options.push({ name: this.translate.instant(`Date-Value.relative-date-range`), value: DateValueType.RelativeDate });
    options.push({ name: this.translate.instant(`Date-Value.specific-date-range`), value: DateValueType.SpecificDate });

    this.typeField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: new FixedDataSource(options),
    });
  };
}
