import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import {
  DataSourceLookup,
  DelayService,
  FieldDefinition,
  IFieldDataSource,
  IFieldOptions,
  MetaDataType,
} from '../../../../../../../../Bamba.Shared';
import { DataSource } from '../../../../blocks/models/datasource';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-datasource-lookup',
  templateUrl: './datasource-lookup.component.html',
  styleUrls: ['./datasource-lookup.component.scss'],
})
export class DatasourceLookupComponent implements OnInit {
  @Output() reloadLookup = new EventEmitter<string>();
  @Input() datasourceLookup: DataSourceLookup;
  @Input() metaDataMappingOptions: IFieldOptions[];

  fieldDatasourceLookup: DataSourceLookup;

  datasourcesField: FieldDefinition;
  datasource: DataSource;
  loadingDatasources = false;

  mappingOptionsColumn: IFieldOptions[];

  constructor(
    private translate: TranslateService,
    private hydra: HydraApiService,
    private toastr: ToastrService,
    private delayService: DelayService
  ) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields = (): void => {
    const datasourcesDataSource: DatasourcesDataSource = new DatasourcesDataSource(this.datasourceLookup, this.hydra);

    this.datasourcesField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-delivery-datasource.datasources'),
      placeholder: this.translate.instant('step-delivery-datasource.datasources-placeholder'),
      datasource: datasourcesDataSource,
      required: true,
    });
  };

  onDatasourceSelected = (value: string) => {
    if (!value || !this.datasourceLookup.dataSourceId) return;

    this.reloadLookup.emit(this.datasource?.id);
    this.loadingDatasources = true;
    this.hydra.getDataSource(value, this.dataSourceSuccessCallBack, this.failureCallBack);
  };

  dataSourceSuccessCallBack = (datasource: DataSource) => {
    this.loadingDatasources = false;

    if (this.datasource) this.datasource.columns = [];

    this.delayService.execute(() => {
      this.datasource = datasource;
      this.mappingOptionsColumn = [];
      datasource.columns.forEach((column, index) => {
        this.mappingOptionsColumn.push({ name: column.name, value: `${column.id}` });
      });
    });
  };

  failureCallBack = (err: any) => {
    this.loadingDatasources = false;
    this.toastr.error(this.translate.instant('step-delivery-datasource.error-loadingdatasource'));
  };
}

class DatasourcesDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  constructor(public options: DataSourceLookup, private hydra: HydraApiService) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: Array<IFieldOptions>) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.hydra.getAllDataSources((datasourcesItems: DataSource[]) => {
      const items: Array<IFieldOptions> = [];

      datasourcesItems.forEach((item: DataSource) => {
        items.push({ name: item.name, value: item.id });
      });

      successCallBack(items);
    }, failureCallBack);
  }
}
