import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { UserStateService } from '../../../blocks/services/user-state.service';
import {
  FieldDefinition,
  IFieldOptions,
  MetaDataDefinition,
  MetaDataType,
  FileBoundLookup,
  CSVLookup,
  MetaDataNoopLookup,
  FixedDataSource,
  DataSourceLookup,
  MetaDataLookupMapping,
} from 'shared-code-lib';

@Component({
  selector: 'app-metadata-lookup',
  templateUrl: './metadata-lookup.component.html',
  styleUrls: ['./metadata-lookup.component.scss'],
})
export class MetadataLookupComponent implements OnInit {
  @Output() optionsLoaded = new EventEmitter();
  @Input() mdd: MetaDataDefinition;

  fieldLookupType: FieldDefinition;
  displayAsList: boolean;

  metaDataMappingOptions: IFieldOptions[];

  get mddCsvLookup() {
    return this.mdd.lookup as CSVLookup;
  }
  get mddFileBoundLookup() {
    return this.mdd.lookup as FileBoundLookup;
  }
  get mddDataSourceLookup() {
    return this.mdd.lookup as DataSourceLookup;
  }

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit() {
    if (this.mdd.lookup == null || this.mdd.lookup instanceof MetaDataNoopLookup) {
      this.mdd.lookup = new FileBoundLookup();
    }
    this.metaDataMappingOptions = this.userState.user.metaData
      .filter((md) => md.id !== this.mdd.id && md.type !== MetaDataType.LineItems && md.type !== MetaDataType.Lookup)
      .map<IFieldOptions>((md) => {
        return { name: md.name, value: md.id };
      })
      .sort((x, y) => x.name.localeCompare(y.name));

    this.metaDataMappingOptions.unshift({
      name: this.translate.instant('admin-metadata-lookup.mapping-none'),
      value: '',
    });

    this.createFields();
  }

  selectedLookupTypeChanged(optionValue: string): void {
    if (optionValue === '0' && !(this.mdd.lookup instanceof FileBoundLookup)) {
      this.mdd.lookup = new FileBoundLookup();
      return;
    }

    if (optionValue === '1' && !(this.mdd.lookup instanceof CSVLookup)) {
      this.mdd.lookup = new CSVLookup();
      this.mdd.lookup.lookupType();
      return;
    }

    if (optionValue === '2' && !(this.mdd.lookup instanceof DataSourceLookup)) {
      this.mdd.lookup = new DataSourceLookup();
      this.mdd.lookup.lookupType();
      return;
    }
  }

  createFields = (): void => {
    const lookupTypeOptions: IFieldOptions[] = [
      { value: '0', name: this.translate.instant('admin-metadata-lookup.type-filebound') },
      { value: '1', name: this.translate.instant('admin-metadata-lookup.type-csv') },
      { value: '2', name: this.translate.instant('admin-metadata-lookup.type-datasource') },
    ];

    this.fieldLookupType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-metadata-lookup.lookup-type'),
      placeholder: this.translate.instant('admin-metadata-lookup.lookup-type'),
      datasource: new FixedDataSource(lookupTypeOptions),
    });
  };

  onLoadOptions() {
    this.optionsLoaded.emit();
  }

  onReloadLookup(prevDataSourceId: string) {
    if (prevDataSourceId && prevDataSourceId != (this.mdd.lookup as DataSourceLookup).dataSourceId)
      this.mdd.lookup.mappings = Array<MetaDataLookupMapping>();
  }
}
