import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { ParseResult } from 'ngx-papaparse';
import { TranslateService } from '@ngx-translate/core';

import { CSVService } from '../../../../blocks/services/csv.service';
import { Filename } from '../../../../blocks/core/fileName';
import { IFieldOptions, CSVLookup } from 'shared-code-lib';
import { Log } from '../../../../blocks/core/log';

@Component({
  selector: 'app-csv-lookup',
  templateUrl: './csv-lookup.component.html',
  styleUrls: ['./csv-lookup.component.scss'],
})
export class CSVLookupComponent implements OnInit {
  @Output() optionsLoaded = new EventEmitter();
  @Input() csvLookup: CSVLookup;
  @Input() metaDataMappingOptions: IFieldOptions[];

  columns: IFieldOptions[];
  dataRowCount: number;
  parsingCsvFile = false;

  importExtensions = ['txt', 'csv'];

  constructor(private csv: CSVService, private toastr: ToastrService, private translate: TranslateService) {}

  ngOnInit() {
    this.extractColumns();
  }

  onFileSelected(event: any) {
    if (!event.target.files || !event.target.files.length) return;

    const file = event.target.files[0];

    if (!Filename.extensionIsOneOf(file.name, this.importExtensions)) {
      this.toastr.error(this.translate.instant('csv.only-csv-and-text'));
      return;
    }

    this.csvLookup.csvFile = file;
    this.csvLookup.fileName = file.name;

    this.parseCSVFile();
  }

  getCSVFile = (): string => {
    if (this.csvLookup.fileName) return this.csvLookup.fileName;

    return this.translate.instant('csv.no-file-selected');
  };

  parseCSVFile() {
    if (!this.csvLookup.csvFile) return;

    this.parsingCsvFile = true;

    this.csv.parse(this.csvLookup.csvFile, false, this.onCSVParseSuccess, this.onCSVParseFailure);
  }

  onCSVParseSuccess = (result: ParseResult): void => {
    this.csvLookup.rowCount = result.data.length;
    this.csvLookup.firstRow = result.data[0];

    this.parsingCsvFile = false;
    this.csvLookup.lookupFieldId = '';
    this.csvLookup.mappings = [];

    this.extractColumns();
  };

  onCSVParseFailure = (err): void => {
    this.parsingCsvFile = false;
    Log.error('CSVLookupComponent.onCSVParseFailure', err);
    this.toastr.error(this.translate.instant('csv.parse-file-error'));
  };

  extractColumns() {
    this.dataRowCount = this.csvLookup.containsHeader ? this.csvLookup.rowCount - 1 : this.csvLookup.rowCount;

    if (!this.csvLookup.firstRow) return;

    this.columns = this.csvLookup.firstRow.map<IFieldOptions>((value: string, index: number) => {
      if (this.csvLookup.containsHeader) {
        return { name: value, value: `${index}` };
      }

      const columnText = this.translate.instant('Metadata-Form.column');
      return { name: `${columnText + (index + 1)} (${value})`, value: `${index}` };
    });

    this.optionsLoaded.emit();
  }

  onContainsHeaderChecked(event: any) {
    this.csvLookup.containsHeader = !this.csvLookup.containsHeader;
    this.extractColumns();
  }
}
