import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';

import { FieldDefinition, FileBoundLookup, IFieldDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { FileBoundProject, FileBoundProjectFieldResult } from '../../../../blocks/models/filebound';
import { ProxyApiService } from '../../../../blocks/services/proxy-api.service';

@Component({
  selector: 'app-filebound-lookup',
  templateUrl: './filebound-lookup.component.html',
  styleUrls: ['./filebound-lookup.component.scss'],
})
export class FileBoundLookupComponent implements OnInit {
  @Output() optionsLoaded = new EventEmitter();
  @Input() fileBoundLookup: FileBoundLookup;
  @Input() metaDataMappingOptions: IFieldOptions[];

  fieldFileboundUrl: FieldDefinition;
  fieldFileboundUsername: FieldDefinition;
  fieldFileboundPassword: FieldDefinition;
  fieldFileboundProject: FieldDefinition;

  projectsDataSource: FileBoundProjectDataSource;
  loadingProjectFields = false;

  lookupFieldOptions: IFieldOptions[];

  constructor(private translate: TranslateService, private proxy: ProxyApiService) {}

  ngOnInit() {
    this.createFields();

    this.lookupFieldOptions = [];
  }

  createFields = (): void => {
    this.projectsDataSource = new FileBoundProjectDataSource(this.fileBoundLookup, this.proxy);

    this.fieldFileboundUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-delivery-filebound.url'),
      placeholder: this.translate.instant('step-delivery-filebound.url-placeholder'),
      required: true,
    });

    this.fieldFileboundUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-delivery-filebound.username'),
      placeholder: this.translate.instant('step-delivery-filebound.username-placeholder'),
      required: true,
    });

    this.fieldFileboundPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translate.instant('step-delivery-filebound.password'),
      placeholder: this.translate.instant('step-delivery-filebound.password-placeholder'),
      required: true,
    });

    this.fieldFileboundProject = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-delivery-filebound.project'),
      placeholder: this.translate.instant('step-delivery-filebound.project-placeholder'),
      datasource: this.projectsDataSource,
      required: true,
    });
  };

  onLoadProjectsURLChanged = (url: string): void => {
    this.fileBoundLookup.fileBoundURL = url;
    this.projectsDataSource.changed.next();
  };

  onLoadProjectsUsernameChanged = (username: string): void => {
    this.fileBoundLookup.username = username;
    this.projectsDataSource.changed.next();
  };

  onLoadProjectsPasswordChanged = (password: string): void => {
    this.fileBoundLookup.password = password;
    this.projectsDataSource.changed.next();
  };

  onProjectSelected = (value: string) => {
    if (!value) return;
    if (!this.fileBoundLookup.fileBoundURL) return;
    if (!this.fileBoundLookup.username) return;

    this.loadingProjectFields = true;

    this.proxy.getFileBoundProjectFields(
      this.fileBoundLookup.fileBoundURL,
      this.fileBoundLookup.username,
      this.fileBoundLookup.password,
      value,
      this.projectFieldsLoaded,
      this.projectFieldsLoadFailed
    );
  };

  onProjectChanged = () => {
    this.fileBoundLookup.lookupFieldId = '';
    this.fileBoundLookup.mappings = [];
  };

  projectFieldsLoaded = (result: FileBoundProjectFieldResult) => {
    this.loadingProjectFields = false;
    this.fileBoundLookup.projectName = result.project.name;

    this.lookupFieldOptions = result.metaDataFields.map<IFieldOptions>((pf) => ({
      name: pf.name,
      value: `${pf.number}`,
    }));

    this.optionsLoaded.emit();
  };

  projectFieldsLoadFailed = () => {
    this.loadingProjectFields = false;
  };
}

class FileBoundProjectDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  constructor(public options: FileBoundLookup, private proxy: ProxyApiService) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    if (!this.options.fileBoundURL || !this.options.username || !this.options.password) {
      failureCallBack(null);
      return;
    }

    this.proxy.getFileBoundProjects(
      this.options.fileBoundURL,
      this.options.username,
      this.options.password,
      (projects: Array<FileBoundProject>) => {
        const items = new Array<IFieldOptions>();

        for (const project of projects) {
          const item = { name: project.name, value: project.id };
          items.push(item);
        }

        successCallBack(items);
      },
      failureCallBack
    );
  }
}
