import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';

import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ToastrService } from 'ngx-toastr';
import {
  CascadingFieldOptions,
  DelayService,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataCascadingOptionsDataSource,
  MetaDataType,
} from 'shared-code-lib';
import { Log } from '../../../blocks/core/log';
import { IDataSource } from '../../../blocks/models/datasource';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-metadata-cascading-options',
  templateUrl: './metadata-cascading-options.component.html',
  styleUrls: ['./metadata-cascading-options.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MetaDataCascadingOptionsComponent implements OnInit {
  @ViewChild(PerfectScrollbarDirective, { static: true })
  directiveRef?: PerfectScrollbarDirective;
  @Input() dataSource: MetaDataCascadingOptionsDataSource;
  @Input() maxOptions: number = 8;

  allDataSources: IDataSource[];

  datasourcesDataSource: FixedDataSource;
  columnsDataSource: FixedDataSource;
  dataFieldsDataSource: FixedDataSource;

  datasourcesField: FieldDefinition;

  datasourceColumnsLoaded: boolean = false;
  dataFieldsLoaded: boolean = false;
  datasourcesLoaded: boolean = false;

  constructor(
    private hydraApi: HydraApiService,
    private translate: TranslateService,
    private errorsService: ErrorsService,
    private delay: DelayService,
    private toastr: ToastrService,
    private userState: UserStateService,
    private spinner: SpinnerStateService
  ) {}

  ngOnInit() {
    this.spinner.show(this.translate.instant('Common.Loading'), document.body);
    this.datasourcesDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.columnsDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.dataFieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.hydraApi.getAllDataSources(this.datasourcesLoadSucceeded, this.datasourcesLoadFailed);
    this.loadMetaData();
    this.datasourcesField = this.createField(
      'Metadata-Form.datasource',
      'Metadata-Form.datasource',
      this.datasourcesDataSource
    );
  }

  datasourcesLoadSucceeded = (datasources: IDataSource[]): void => {
    this.allDataSources = datasources;
    const columnsFields: Array<IFieldOptions> = [];
    for (let i = 0; i < datasources.length; i++) {
      columnsFields.push({ name: this.translate.instant(datasources[i].name), value: datasources[i].id });
    }

    this.datasourcesDataSource.updateOptions(columnsFields);
    this.onDataSourceChanged(this.dataSource.dataSourceId);
    this.datasourcesLoaded = true;
    this.spinner.hide();
  };

  datasourcesLoadFailed = (error: any): void => {
    Log.error('Metadata-Form.loading-datasources-failed', error);
    this.spinner.hide();
  };

  loadMetaData = (): void => {
    const metadataFields: Array<IFieldOptions> = [];

    for (let i = 0; i < this.userState.user.metaData.length; i++) {
      metadataFields.push({
        name: this.userState.user.metaData[i].name,
        value: this.userState.user.metaData[i].id,
      });
    }

    this.dataFieldsDataSource.updateOptions(metadataFields);
    this.dataFieldsLoaded = true;
  };

  createField = (label: string, placeholder: string, datasource: FixedDataSource) => {
    return new FieldDefinition({
      type: MetaDataType.List,
      label: label ? this.translate.instant(label) : '',
      placeholder: placeholder ? this.translate.instant(placeholder) : '',
      required: true,
      datasource: datasource,
    });
  };

  onAddValue = () => {
    if (!this.validate()) return;

    this.dataSource.dataSet.push(new CascadingFieldOptions('', '', ''));

    this.delay.execute(() => {
      this.directiveRef?.scrollToBottom(0, 300);
    }, 100);
  };

  validate(): boolean {
    const validationErrors = [];
    if (this.dataSource.dataSet && this.dataSource.dataSet.length >= this.maxOptions) {
      validationErrors.push('Metadata-Form.max-options');
    }

    if (!validationErrors.length) return true;

    this.errorsService.showErrorsModal(validationErrors, 'Metadata-Form.items-validation-errors');
    return false;
  }

  deleteAllItems = (): void => {
    this.dataSource.dataSet = [];

    this.delay.execute(() => {
      this.directiveRef?.scrollToTop(0, 300);
    }, 100);
  };

  deleteItem = (item) => {
    const rowIndexToDelete = this.dataSource.dataSet.indexOf(item);

    if (rowIndexToDelete > -1) {
      this.dataSource.dataSet.splice(rowIndexToDelete, 1);
    }
  };

  onDataSourceChanged = (value: string): void => {
    if (!this.allDataSources) return;
    const dataSource = this.allDataSources.find((x) => x.id == value);
    if (!dataSource) return;

    const columnsFields: Array<IFieldOptions> = [];
    for (let i = 0; i < dataSource.columns.length; i++) {
      columnsFields.push({ name: this.translate.instant(dataSource.columns[i].name), value: dataSource.columns[i].id });
    }
    this.columnsDataSource.updateOptions(columnsFields);
    this.datasourceColumnsLoaded = true;
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.dataSource.dataSet, event.previousIndex, event.currentIndex);
  }
}
